import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import Workbench from './containers/Workbench/Workbench';
import Settings from './containers/Settings/Settings';
import Login from './containers/Login/Login';
import Register from './containers/Register/Register';

import PrivateRoute from './hoc/PrivateRoute/PrivateRoute';

const Routes = () => (
  <Switch>
    <Route exact path="/donko/register" component={Register} />
    <Route exact path="/donko/login" component={Login} />
    <PrivateRoute exact path="/donko/settings" component={Settings} />
    <PrivateRoute exact path="/donko" component={Workbench} />
    <Redirect to="/donko" />
  </Switch>
);

export default Routes;
